"use client"

import React from "react"
import {
  Card,
  CardHeader,
  CardTitle,
  CardDescription,
  CardContent,
  CardFooter,
} from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import {
  FormItem,
  FormLabel,
  FormControl,
  FormDescription,
  FormMessage,
} from "@/components/ui/form"
import { Input } from "@/components/ui/input"
import { useForm } from "react-hook-form"
import { Wifi, Building, Bed, Star } from "lucide-react"

type Bike = {
  id: number
  name: string
  description: string
  pricePerHour: number
  location: string
  features: string[]
  rating: number
  image: string
  gallery: string[]
}

type FormData = {
  name: string
  email: string
  hours: number
}

type BikeDetailProps = {
  bike: Bike
  onSubmit: (data: FormData) => void
  register: any
  errors: any
  handleSubmit: any
  disabled: boolean
}

export default function BikeDetail({
  bike,
  onSubmit,
  register,
  errors,
  handleSubmit,
  disabled,
}: BikeDetailProps) {
  return (
    <section className="bg-gray-800 rounded-lg p-6 shadow-md">
      <div className="flex justify-between items-center mb-4">
        <h2 className="text-3xl font-bold text-white">{bike.name}</h2>
        <div className="flex items-center space-x-2 bg-purple-700 text-white rounded px-3 py-1 text-sm font-semibold">
          <span>{bike.rating.toFixed(1)}</span>
          <Star className="w-5 h-5 text-yellow-400" />
        </div>
      </div>
      <p className="text-gray-300 mb-2">{bike.location}</p>
      <p className="text-gray-300 mb-4">{bike.description}</p>
      <div className="flex space-x-4 mb-4">
        {bike.features.map((feature, idx) => (
          <div
            key={idx}
            className="flex items-center space-x-1 bg-gray-700 rounded px-3 py-1 text-sm"
          >
            {feature === "Wifi" && <Wifi className="w-5 h-5" />}
            {feature === "Garage" && <Building className="w-5 h-5" />}
            {(feature === "3 habitaciones" ||
              feature === "4 habitaciones" ||
              feature === "2 habitaciones") && <Bed className="w-5 h-5" />}
            <span>{feature}</span>
          </div>
        ))}
      </div>
      <div className="mb-6">
        <h3 className="text-xl font-semibold text-white mb-2">GalerÃ­a</h3>
        <div className="grid grid-cols-3 gap-4">
          {bike.gallery.map((img, idx) => (
            <img
              key={idx}
              src={img}
              alt={`${bike.name} gallery ${idx + 1}`}
              className="rounded-lg object-cover h-32 w-full"
            />
          ))}
        </div>
      </div>
      <form onSubmit={handleSubmit(onSubmit)} className="space-y-4">
        <FormItem>
          <FormLabel htmlFor="name">Name</FormLabel>
          <FormControl>
            <Input
              id="name"
              {...register("name", { required: "Name is required" })}
              placeholder="Your full name"
            />
          </FormControl>
          <FormDescription>Your full name</FormDescription>
          <FormMessage>{errors.name && errors.name.message}</FormMessage>
        </FormItem>

        <FormItem>
          <FormLabel htmlFor="email">Email</FormLabel>
          <FormControl>
            <Input
              id="email"
              type="email"
              {...register("email", {
                required: "Email is required",
                pattern: {
                  value: /^\\S+@\\S+$/i,
                  message: "Invalid email address",
                },
              })}
              placeholder="Your email address"
            />
          </FormControl>
          <FormDescription>We will send the rental details to this email</FormDescription>
          <FormMessage>{errors.email && errors.email.message}</FormMessage>
        </FormItem>

        <FormItem>
          <FormLabel htmlFor="hours">Hours to Rent</FormLabel>
          <FormControl>
            <Input
              id="hours"
              type="number"
              min={1}
              {...register("hours", {
                required: "Number of hours is required",
                min: { value: 1, message: "Minimum 1 hour" },
              })}
              placeholder="Number of hours"
            />
          </FormControl>
          <FormDescription>Enter the number of hours you want to rent the bike</FormDescription>
          <FormMessage>{errors.hours && errors.hours.message}</FormMessage>
        </FormItem>

        <Button type="submit" className="mt-4" disabled={disabled}>
          Book Now
        </Button>
      </form>
    </section>
  )
}
